<template>
  
    <el-container>
     
      <el-main>
       <h1>创建 basic 模式思维导图</h1>
       <h2>方法 1</h2>
       <p>在 markdown 文件中添加 yaml :</p>
       <pre>
        <code>
---

mindmap-plugin: basic

---
        </code>
       </pre>
       <p>在更多菜单中，你可以找到 <i>打开为思维导图</i></p>
       <div>
          <img src="../assets/markmind/basic.gif" alt="" width="800">
       </div>

       <h2>方法 2</h2>
       <p>在文件夹上右击，点击 <i>新建思维导图</i> 菜单</p>

        <div>
          <img src="../assets/markmind/basic2.gif" alt="" width="800">
       </div>

       <h3>basic 模式输出以下格式的 markdown:</h3>

       <pre>
        <code>
---

mindmap-plugin: basic

---

# Mark mind for obsidian

## Links
- https://github.com/MarkMindLtd/obsidian-markmind
- [GitHub](https://github.com/MarkMindLtd/obsidian-markmind)

## Related
- [coc-markmap](https://github.com/gera2ld/coc-markmap)
- [gatsby-remark-markmap](https://github.com/gera2ld/gatsby-remark-markmap)

## Features
- links
- **inline** ~~text~~ *styles*
- multiline
   text
- `inline code`
- Katex - $x = {-b \pm \sqrt{b^2-4ac} \over 2a}$
        </code>
       </pre>

      </el-main>
    </el-container>
 
</template>

<script>
export default {
  name: 'BasicModePage',
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
